<!-- 普通用户--我的待办 -->
<script setup>
import { ref, reactive, onMounted, onUnmounted } from "vue";
// import { FormOutlined } from '@ant-design/icons-vue';
import { message } from "ant-design-vue";
import { useRouter } from "vue-router";
import {
  getTodoListApi,
  getUserListApi,
  getEvaluatedListApi,
  getAssessorApi,
} from "@/api/todoList";
import { notification } from "ant-design-vue";

onMounted(() => {
  getTodoList();
  getUserList();
  updateScrollHeight();
  window.addEventListener("resize", updateScrollHeight);

  getAssessor();

  // 页面加载时调用通知
  // openNotificationWithIcon('info', '您可点击“我要评估”按钮，自行添加您想评估的人员，每轮评估可选择0-3人进行打分哦~(仅在第一二阶段可添加)^_^');
});
// 进入页面提示可添加“我要评估”
const openNotificationWithIcon = (type, message) => {
  notification[type]({
    message: "小提示",
    description: message,
    duration: 300, // 设置持续时间为 null，表示通知不自动关闭
    placement: "top", // 设置位置为顶部居中
    style: {
      fontSize: "18px", // 直接修改字体大小
    },
  });
};

// 在组件卸载时移除事件监听
onUnmounted(() => {
  window.removeEventListener("resize", updateScrollHeight);
});

const scrollHeight = ref("60vh"); // 定义滚动区域高度
const router = useRouter();
const pagination = reactive({
  // 表格页码相关设置
  pageSize: 11, //每页最多条目数
  current: 1, //当前页码
  showSizeChanger: false,
  total: 0, //总条目数
  showTotal: (total, range) => `总共 ${total} 条数据`, // 在这里定义显示总数据量的格式
});
const columns = ref([
  {
    title: "序号",
    dataIndex: "index",
    key: "index",
    width: "10%",
    align: "center",
  },
  {
    title: "任务类型",
    dataIndex: "taskTag",
    key: "taskTag",
    width: "15%",
    align: "center",
  },
  {
    title: "任务详情",
    dataIndex: "taskDetail",
    key: "taskDetail",
    width: "30%",
    align: "center",
  },
  {
    title: "提交时间",
    dataIndex: "submitTime",
    key: "submitTime",
    align: "center",
  },
  {
    title: "操作",
    key: "action",
    align: "center",
  },
]);
const data = ref([]);

// 动态更新高度
const updateScrollHeight = () => {
  const screenHeight = window.innerHeight;
  const containerHeight = screenHeight - 35 - 10; // 假设父容器需要减去固定的偏移量
  if (screenHeight < 800) {
    scrollHeight.value = `${containerHeight * 0.63}px`; // 小屏幕高度为 50%
  } else {
    scrollHeight.value = `${containerHeight * 0.7}px`; // 大屏幕高度为 80%
  }
};
// 调用api-获取待办列表
const getTodoList = () => {
  // 清空 data.value
  data.value.splice(0, data.value.length);
  // 处理当前页码
  if (sessionStorage.getItem("currentIndex")) {
    pagination.current = sessionStorage.getItem("currentIndex");
  }
  getTodoListApi(pagination.current, pagination.pageSize)
    .then((res) => {
      if (res.data.code === 200) {
        // 设置分页总数
        pagination.total = res.data.data.total;

        // 清空 data.value，避免多次追加导致重复数据
        data.value = [];

        // 筛选数据：仅保留 operation 为 0 的项
        const filteredData = res.data.data.data.filter(
          (item) => item.operation === 0
        );

        // 映射数据，格式化并追加到 data.value
        data.value.push(
          ...filteredData.map((item, index) => ({
            key: item.id,
            index: index + 1,
            taskTag: item.type,
            taskDetail: item.detail,
            submitTime: item.presentDate,
            operation: item.operation,
            evaluatedName: item.evaluatedName,
            department: item.department,
          }))
        );
      } else {
        // 处理非成功响应
        message.error("获取失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      // 捕获并处理请求错误
      message.error("请求失败，请稍后重试！");
    });
};

// 设置三种任务标签的颜色
const getTagColor = (taskTag) => {
  if (taskTag === "添加评估人审批") return "cyan";
  if (taskTag === "添加被评估人审批") return "orange";
  if (taskTag === "周边评议") return "purple";
  return "green";
};
// 调用api-进行单条任务的同意
const handleAgreeOne = (record) => {
  // 1、获取传入接口的任务id
  // 2、调用单条同意接口
  message.success("同意成功！");
};
// 处理翻页
const handleTableChange = (e) => {
  pagination.current = Number(e.current); //更新当前页码
  pagination.pageSize = e.pageSize; //更新每页显示条目数
  sessionStorage.setItem("currentIndex", pagination.current);
  getTodoList(); // 重新获取列表数据
};
const openRefuse = ref(false); //拒绝理由输入框默认不展示
const showModal = () => {
  openRefuse.value = true;
};
const handleEnterQues = (record) => {
  // 1、获取record中的被评估人信息（姓名+部门）
  const evaluatedName = record.evaluatedName;
  const department = record.department;
  const todoListId = record.key;
  // 2、通过session传递信息
  sessionStorage.setItem("evaluatedName", evaluatedName);
  sessionStorage.setItem("department", department);
  sessionStorage.setItem("todoListId", todoListId);
  // 3、页面跳转
  if (localStorage.getItem("userRole") === "0") {
    router.push({
      path: "/quesnaireEdit",
    });
  } else {
    router.push({
      path: "/quesnaireEditAd",
    });
  }
};
const openDetail = ref(false); //查看详情弹窗默认不展示
const showDetail = () => {
  openDetail.value = true;
};
const openEvaluator = ref(false); //添加被评估人弹窗默认不展示
const showEvaluator = () => {
  openEvaluator.value = true;
};
const evaluator = ref([]);
const options = ref([]);
// 获取人员列表
const getUserList = () => {
  getUserListApi()
    .then((res) => {
      if (res.data.code === 200) {
        options.value = [];
        for (let i = 0; i <= res.data.data.length - 1; i++) {
          options.value.push({
            value:
              res.data.data[i].name + "(" + res.data.data[i].department + ")",
            key: res.data.data[i].userId,
          });
        }
      } else {
        message.error("获取失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！", err);
    });
};
// 调用api-将选择的被评估人key值传给后端
const handleEvaluatorOk = () => {
  // 1、获取被评估人key值
  const selectedKeys = evaluator.value.map(
    (item) => options.value.find((opt) => opt.value === item).key
  );
  if (selectedKeys.length === 0) {
    message.warn("您还未选择被评估人哦𖦹𖦹 .ᐟ.ᐟ ");
    return;
  }
  if (selectedKeys.length > 3) {
    message.warn("最多只能选择三个被评估人哦𖦹𖦹 .ᐟ.ᐟ ");
    return;
  }
  // 2、调用添加被评估人接口（若添加成功，清除evaluator值）
  getEvaluatedListApi(selectedKeys)
    .then((res) => {
      if (res.data.code === 200) {
        message.success("添加成功！");
        evaluator.value = [];
        getAssessor(); // 刷新被评估人表格
        getTodoList(); // 刷新待办表格
        openEvaluator.value = false; // 3、关闭弹窗
      } else {
        message.error("添加失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！", err);
    });
};

// 添加自主评估人相关配置
const columnsDetail = ref([
  {
    title: "序号",
    dataIndex: "index",
    key: "index",
    align: "center",
  },
  {
    title: "姓名",
    dataIndex: "username",
    key: "username",
    align: "center",
  },
  {
    title: "部门",
    dataIndex: "department",
    key: "department",
    align: "center",
  },
]);
const dataDetail = ref([]);
const getAssessor = () => {
  getAssessorApi()
    .then((res) => {
      if (res.data.code === 200) {
        dataDetail.value = [];
        for (let i = 0; i <= res.data.data.length - 1; i++) {
          dataDetail.value.push({
            index: i + 1,
            username: res.data.data[i].username,
            department: res.data.data[i].department,
          });
        }
      } else {
        message.error("获取失败！" + res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！", err);
    });
};
</script>

<template>
  <div class="todoList-container">
    <div class="left">
      <!-- 标题区域 -->
      <div class="title">
        <span>我的待办</span>
      </div>
      <!-- 内容区域 -->
      <div class="todoTable">
        <!-- 待办表格 -->
        <a-table
          :columns="columns"
          :data-source="data"
          :rowKey="(record) => record.key"
          :scroll="{ y: scrollHeight }"
          :pagination="pagination"
          :bordered="true"
          @change="handleTableChange"
        >
          <template #bodyCell="{ column, record }">
            <!-- 设置三种任务标签的颜色 -->
            <template v-if="column.key === 'taskTag'">
              <span>
                <a-tag
                  :key="record.taskTag"
                  :color="getTagColor(record.taskTag)"
                >
                  {{ record.taskTag }}
                </a-tag>
              </span>
            </template>
            <!-- 设置操作栏 -->
            <template v-if="column.key === 'action'">
              <span v-if="record.taskTag === '添加评估人审批'">
                <a
                  style="color: #0014b7; text-decoration: underline"
                  @click="showDetail"
                  >查看详情</a
                >
                <a-divider type="vertical" />
                <a style="color: #bd3124; font-weight: bold" @click="showModal"
                  >拒绝</a
                >
              </span>
              <span v-else-if="record.taskTag === '添加被评估人审批'">
                <a
                  style="color: #3f9373; font-weight: bold"
                  @click="handleAgreeOne(record)"
                  >同意</a
                >
                <a-divider type="vertical" />
                <a style="color: #bd3124; font-weight: bold" @click="showModal"
                  >拒绝</a
                >
              </span>
              <span v-else-if="record.taskTag === '周边评议'">
                <a
                  style="color: #3f9373; font-weight: bold"
                  @click="handleEnterQues(record)"
                  >进入评议</a
                >
              </span>
            </template>
          </template>
        </a-table>
      </div>
    </div>
    <div class="right">
      <a-button type="primary" @click="showEvaluator"> 我要评估 </a-button>
      <!-- 我要评估表格 -->
      <div style="margin-top: 10px">
        <a-table
          :columns="columnsDetail"
          :data-source="dataDetail"
          :bordered="false"
          :pagination="false"
          :scroll="{ y: '60vh' }"
        >
        </a-table>
      </div>

      <!-- 添加被评估人弹窗 -->
      <a-modal
        v-model:open="openEvaluator"
        title="添加被评估人"
        @ok="handleEvaluatorOk"
      >
        <span style="color: #bd3124">注：您可以自主选择1-3个要评估的人</span>
        <a-select
          v-model:value="evaluator"
          :options="options"
          mode="multiple"
          placeholder="请选择"
          style="width: 100%"
        >
        </a-select>
      </a-modal>
    </div>
  </div>
</template>

<style lang="less" scoped>
.todoList-container {
  // border: 1px solid red;
  display: flex;
  width: 100%;
  height: 100%;
  gap: 10px;
}

.left {
  border: 1.5px dashed rgba(128, 191, 255, 0.7);
  width: 70%;
  padding: 10px;
  /* 给左侧区域加点内边距 */

  .title {
    // border: 1px solid blue;
    height: 35px;
    display: flex;
    justify-content: space-between;
    margin: 0px 10px 10px 10px;

    span {
      font-size: 16px;
      font-weight: bold;
      display: flex;
      align-items: center;
    }
  }

  .todoTable {
    height: calc(100% - 35px - 10px);
    // border: 1px solid red;
    margin: 0 10px 0 10px;
    overflow: hidden;
    /* 防止滚动条溢出 */
  }
}

.right {
  // border: 1px solid orange;
  width: 30%;
  padding: 15px;
  background-color: rgba(179, 217, 255, 0.3);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
</style>